import { createSlice } from "@reduxjs/toolkit";

export const usersSlice = createSlice({
  name: "users",
  initialState: {
    username: "",
    password: "",
    mail: "mario@gmail.com",
    name: "Usuario",
    Des: "",
    number: "44",
    phone: "",
    description: " holala clasdflasdf asdf asd fasd fa sdf",
    img: [],
  },
  reducers: {
    sLogin: (state, action) => {
      const { username, password } = action.payload;
      if (username && password) {
        state.username = username;
        state.password = password;
      }
    },
    sPhone: (state, action) => {
      const phone = action.payload;
      if (phone) {
        state.phone = phone;
      }
    },
    sMail: (state, action) => {
      const mail = action.payload;
      if (mail) {
        state.mail = mail;
      }
    },
    sD: (state, action) => {
      const sd = action.payload;
      if (sd) {
        state.Des = sd;
      }
    },
    sName: (state, action) => {
      const name = action.payload;
      console.log(name);
      if (name) {
        state.name = name;
      }
    },
    sDescription: (state, action) => {
      const { number, description } = action.payload;
      if (number && description) {
        state.number = number;
        state.description = description;
      }
    },
  },
});

// Action creators are generated for each reducer function
export const { sName, sMail, sPhone, sLogin, sDescription, sD } = usersSlice.actions;
export default usersSlice.reducer;
