import React from "react";

function Loading() {
  return (
    <>
      <div className="modal waitingFor" style={{display: "block"}}>
        <div className="container mt50" align="center">
          <p className="f23 white mb40">Espere por favor...</p>
          <div className="cssload-container">
            <div className="cssload-speeding-wheel"></div>
          </div>
        </div>
      </div>
    </>
  );
}

export default Loading;
