import React from "react";
import i00 from "./img/icons/error.png";

function I({ sP, sS, i }) {
  const [isActive, setIsActive] = React.useState(false);
  const [time, setTime] = React.useState(0);
  const [cont, setCont] = React.useState(0);

  const handleButton = () => {
    setIsActive(true);
    sS(true);
  };

  React.useEffect(() => {
    let interval = null;
    if (time >= 5) {
      sS(false);
      setIsActive(false);
      setCont(cont + 0);
      setTime(0);
      sP(0);
    }

    if (isActive) {
      interval = setInterval(() => {
        setTime((time) => time + 1);
      }, 1000);
    } else {
      clearInterval(interval);
    }
    return () => {
      clearInterval(interval);
    };
  }, [isActive, time, cont]);

  return (
    <>
      <div id="login" className="login">
        <br />
        <br />
        <h4 className="blue mb30" align="center">
          <em>Acceso a Banca Digital</em>
        </h4>
        <br />

        <div id="error">
          <div align="center">
            <p className="white">Acceso Inválido</p>
          </div>

          <div
            className="mb30  "
            style={{
              backgroundColor: "#0090D7",
              width: "100%",
              padding: "10px",
            }}
          >
            <img src={i00} align="center" />
          </div>
          <div className="white">
            <p>Verifique los datos para su correcto funcionamiento.</p>

            <p>Número de intentos: {i}</p>

            <br />
            <br />

            <div align="center">
              <button
                id="nuevoIntento"
                onClick={handleButton}
                className="btn btn-regresar bg-blue"
              >
                Intentar de Nuevo
              </button>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}

export default I;
